<script>
   /*if(window.screen.width<=960){
      v.phoneToggle==false;
   }*/
   export default {
      name: "navi",
      components: {},
      data() {
         return {
            phoneToggle: true,
            styleobj: {
               fontSize: "30px",
               color: "red"
            }
         };
      },
      created(){
         if (document.body.clientWidth <= 960) {
            this.phoneToggle = false;
         }
      }
   };
</script>

