      }
   }
   .slide-fade-enter-active {
      transition: all 0.2s ease;
   }
   .slide-fade-leave-active {
      transition: all 0.2s ease;
   }
   .slide-fade-enter,
   .slide-fade-leave-to {
      transform: translateX(10px);
      opacity: 0;
   }
</style>
